@import "1-base/base-index";
@import "2-components/forms";
@import "2-components/icons";
@import "2-components/links";
@import "2-components/tabs";
@import "2-components/avatars";
@import "2-components/lists";
@import "2-components/tables";
@import "2-components/widgets";
@import "3-blocks/blocks-index";
@import "4-layout/layout-index";
@import "5-woocommerce/woocommerce-index";
@import "7-editor/woocommerce";

/* Link underlines */
.wp-block-search__button {
	@include btn-s;
}

// Button styles
.wp-block-button__link,
.wc-block-components-button:not(.is-link).contained,
.mc4wp-form-fields p input[type="submit"] {
	@include btn;
	@include transition-default;
}

// Default Buttons
input[type="submit"],
button[type="submit"],
.primary .wp-block-button__link {
	@include btn-default;
}

.primary-variant .wp-block-button__link,
.btn-style-variant input[type="submit"] {
		@include btn-default-variant;
}


// Special button styles
.wp-block-button.stretch,
.wp-block-button.stretch .wp-block-button__link {
	width: 100% !important;
}

// Classed to set all default buttons to rounded or round.
.btn-rounded {

	.btn,
	input[type="submit"],
	button[type="submit"],
	.edit,
	.woocommerce-button,
	.wc-block-components-button,
	.form-submit.wp-block-button input,
	.woocommerce-MyAccount-navigation li a,
	.tag-cloud-link,
	li a {
		border-radius: var(--global--border-radius-xxs);
	}
}

.btn-round {

	.btn,
	.edit,
	input[type="submit"],
	button[type="submit"],
	.woocommerce-button,
	.search-header .search-field,
	.wc-block-components-button,
	.form-submit.wp-block-button input,
	.woocommerce-MyAccount-navigation li a,
	.tag-cloud-link,
	li a {
		border-radius: var(--global--border-radius-round);
	}
}

// WooCommerce
.woocommerce-orders-table .woocommerce-button {
	@include btn;
	@include btn-xxs;
}

.wc-block-grid__products {
	margin: 0 !important;
}
